.root {
  padding-left: 2%;
  padding-right: 2%;
  width: 100%;

  & > div {
    padding: 0;
    & > div > div {
      & > div {
        font-weight: 700;
        font-size: 10px;
        line-height: 12px;

        color: white;
        svg {
          width: 10px;
          height: auto;
        }
      }
    }
  }
  & > div:first-child {
    span {
      font-weight: 700;
      font-size: 7.79336px;
      line-height: 10px;
      color: white;
    }
    img {
      width: 16px;
      height: 16px;
      transform: scale(1);
    }
    h5 {
      font-size: 12px;
    }
  }
  @media (max-width: 600px) {
    & > div {
      & > div > div {
        & > div {
          font-size: 7.79336px;
          line-height: 10px;
        }
      }
    }
  }
}
.field {
  padding: 0 10px;
  height: 24px;
}
